import { Text } from "../../containers/Language";

const DownloadDescription = (props) => {
  return (
    <div className="download_description" {...props}>
      <div className="download_description_title">
        <Text tid="pptHeader" />
      </div>
      <div className="download_description_subtitle">
        <Text tid="pptDescription" />
      </div>
    </div>
  );
};
export default DownloadDescription;
